.App {
  text-align: center;
}
.main-bg {
  height: 300px;
  background-image: url('../public/img/bg.png');
  background-size: cover;
  background-position: center;
}
.link-page:visited {
  color: gray;
}
.not-exist-detail {
  margin-top: 20px;
  color: red;
}
.fade-start {
  opacity: 0;
}
.fade-end {
  opacity: 1;
  transition: opacity, 0.7s;
}
.black {
  color: black;
}
th,
td {
  vertical-align: middle;
}
.cursor {
  cursor: pointer;
}
.wrapper {
  position: relative;
}
.fixed {
  position: fixed;
}
.nav-position {
  width: 100vw;
  top: 0;
  left: 0;
  z-index: 2;
}
.viewed-position {
  width: 13vw;
  top: 370px;
  right: 10px;
  opacity: 0.75;
}
.main-content {
  position: absolute;
  top: 56px;
  width: 100vw;
}
.not-focus:focus {
  outline: none;
}
.not-display {
  display: none;
}
.total-price {
  font-size: 18px;
  font-weight: 600;
  text-align: right;
  margin-right: 30px;
}
